.game-section
{
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding: 1vw;
  background-color: var(--background-color);
  margin: 0;
  overflow: hidden;
}

.game-container-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1em;
  background-color: var(--background--1);
  border-radius: 0.8vw;
  padding: 1.5vw;
  box-shadow: 0 0.4vw 0.6vw rgba(0,0,0,0.1);
  height: calc(100% - 26vh); /* 13vh pour le header + 13vh pour les cartes */
  width: 100%;
}

.game-container {
  display: flex;
  gap: 1vw;
  justify-content: space-evenly;
  align-items: center;
  flex: 1;
 
}


.ia-panel-content {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: var(--background--2);
  min-width: 30vw;
  border-radius: 0.8vw;
  padding: 0.5em;
}

.ia-settings {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-evenly;
  gap: 1vw;
  color: var(--text-second);
}

.ia-toggle {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5vw;
}

.ia-toggle label {
  font-size: 1.1em;
  color: var(--text-second);
}

.ia-toggle input[type="checkbox"] {
  width: 1.5em;
  height: 1.5em;
  cursor: pointer;
}

.difficulty-settings {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5vw;
}

.difficulty-settings label {
  font-size: 1.1em;
  color: var(--text-second);
}

.difficulty-settings select {
  width: 100%;
  padding: 0.5em;
  border-radius: 0.3em;
  background-color: var(--background--2);
  color: var(--text-second);
  border: 1px solid var(--text-second);
  cursor: pointer;
}

.evaluation-panel {
  flex: 1;
  width: 25vw;
  height: 100%;
  background-color: var(--secondary-color);
  border-radius: 0.8vw;
  padding: 1.5vw;
  box-shadow: 0 0.4vw 0.6vw rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.chessboard-container {
  flex: 2;
  width: 50vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1vw;
  position: relative;
}


.recap-container {
  flex: 1;
  width: 25vw;
  height: 100%;
  background-color: var(--background--2);
  border-radius: 0.8vw;
  padding: 1.5vw;
  box-shadow: 0 0.4vw 0.6vw rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
  padding: 2vw;
  color: var(--text-main);
  font-size: 1.2vw;
}

.board_recap-container
{
  display: flex;
  flex-direction: column;
  gap: 2vw;
  width: 100%;
  max-width: 80vw;
  margin: 0 auto;
  padding: 2vw;
}

.move-recap-component
{
  background-color: var(--background--1);
  border-radius: 5px;
  padding: 5px;
  min-width: 20vw;
  max-height: 50vh;
  max-width: 40vw;
  display: flex;
  flex-direction: column;
  gap: 0.2em;
  padding: 0.5em;
 
  color: var(--text-second);
  opacity: 0.85;
}


.display-move-recap
{
  display: flex;
  align-items: left;
  justify-content: center;
  max-width: auto;
  padding: 5px;
}

.game-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: auto;
    gap: 0.5em;
    background-color: var(--background--2);
    color: var(--text-second);
    border: 2px solid var(--text-second);
    border-radius: 5px;
    padding: 0.25em;
    margin-bottom: 0.25em;
  }