/* src/App.css */

*,*::before,*::after {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html, body {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
}

#root {
    width: 100vw;
    height: 100vh;
    margin: 0;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center ;
    background-color: var(--background--1);
    height: 100vh;
    width: 100vw;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    gap: 1vw;
    color: var(--text-second);
    text-align: center;
    overflow: hidden;
   
}


  


@import './styles/variables.css';
@import './styles/layout.css';
@import './styles/header.css';
@import './styles/card.css';
@import './styles/chessboard.css';

@import './styles/buttons.css';
@import './styles/status.css';
@import './styles/game-section.css';

@import './styles/popup-overlay.css';
@import './styles/learn-section.css';
@import './styles/openingBoard.css';





@import './styles/media.css';













:root {
    --primary: #e2b857;
    --secondary: #2e2f4f;
    --background-gradient: linear-gradient(135deg, #181c2b 0%, #23243a 60%, #31204b 100%);
    --panel-bg: rgba(34, 37, 57, 0.97);
    --card-bg: rgba(34, 37, 57, 0.92);
    --text-main: #f7f7f7;
    --text-soft: #bdbecb;
    --accent: #e2b857;
    --shadow: 0 6px 32px 0 rgba(0,0,0,0.18);
    --border-radius: 1.2em;
    --header-height: 10vh;
    --cards-height: 20vh;
    --main-height: 70vh;
  }
  
  /* Fond général */
  body, #root, .App {
    min-height: 100vh;
    min-width: 100vw;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', Verdana, Geneva, Tahoma, sans-serif;
    background: var(--background-gradient);
    color: var(--text-main);
    box-sizing: border-box;
    overflow: hidden;
  }
  
  /* HEADER */
  .App-header, .header {
    height: var(--header-height);
    width: 100vw;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 0 3vw;
    gap: 2vw;
  }
  .logo {
    height: 54px;
    margin-right: 2vw;
  }
  .site-title {
    font-size: 2.6em;
    font-weight: 800;
    color: var(--primary);
    letter-spacing: 1px;
    text-shadow: 0 2px 12px #000a;
    flex: 1;
    text-align: left;
  }
  .site-subtitle {
    font-size: 1.2em;
    color: var(--text-soft);
    margin-left: 2vw;
  }
  
  /* CARDS SECTION */
  .cards-section {
    height: var(--cards-height);
    width: 100vw;
    display: flex;
    gap: 2vw;
    justify-content: center;
    align-items: stretch;
    background: transparent;
    margin-bottom: 1vh;
  }
  .card {
    background: var(--card-bg);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    padding: 2.2vw 2vw 1.5vw 2vw;
    min-width: 20vw;
    max-width: 24vw;
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    transition: box-shadow 0.2s;
  }
  .card:hover {
    box-shadow: 0 8px 40px 0 #e2b85744;
  }
  .card-icon {
    font-size: 2.8em;
    color: var(--primary);
    margin-bottom: 0.6em;
  }
  .card-title {
    font-size: 1.3em;
    color: var(--accent);
    font-weight: 700;
    margin-bottom: 0.2em;
  }
  .card-desc {
    color: var(--text-soft);
    font-size: 1em;
    text-align: center;
  }
  
  /* MAIN GAME SECTION */
  .game-section, .desktop-display {
    height: var(--main-height);
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
    gap: 2vw;
    background: transparent;
  }
  
  .evaluation-panel, .recap-container {
    background: var(--panel-bg);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    width: 19vw;
    min-width: 16vw;
    padding: 2vw 1.5vw 1vw 1.5vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-top: 1vh;
  }
  .evaluation-panel h3, .recap-container h3 {
    color: var(--primary);
    font-size: 1.25em;
    margin-bottom: 1vw;
    font-weight: 700;
    letter-spacing: 1px;
  }
  .info-item label, .move-recap-list {
    color: var(--text-soft);
    font-size: 1em;
  }
  .score-value {
    color: var(--accent);
    font-size: 1.15em;
    font-weight: 700;
  }
  .move-description {
    color: var(--primary);
    font-weight: 700;
  }
  
  /* CHESSBOARD */
  .chessboard-container {
    background: var(--panel-bg);
    border-radius: var(--border-radius);
    box-shadow: var(--shadow);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 1vw 0.5vw 1vw 0.5vw;
    min-width: 32vw;
    max-width: 36vw;
    margin-top: 1vh;
    position: relative;
  }
  
  /* PANEL IA */
  .ia-panel-content {
    background: linear-gradient(90deg, #23243a 80%, #e2b85711 100%);
    border-radius: 0.8vw;
    box-shadow: 0 0.2vw 0.8vw #0006;
    padding: 0.7em 2em;
    margin-bottom: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2vw;
    min-width: 28vw;
    max-width: 34vw;
  }
  .ia-settings label {
    color: var(--primary);
    font-weight: 600;
    margin-right: 0.6em;
  }
  .ia-settings select, .ia-settings input[type='checkbox'] {
    background: #181c2b;
    color: var(--text-main);
    border: 1px solid var(--primary);
    border-radius: 0.4em;
    padding: 0.2em 0.7em;
    font-size: 1em;
    margin-left: 0.2em;
  }
  
  @media (max-width: 900px) {
    .cards-section, .game-section, .desktop-display {
      flex-direction: column;
      align-items: stretch;
      gap: 2vh;
    }
    .evaluation-panel, .recap-container, .chessboard-container {
      width: 95vw;
      min-width: 0;
      margin: 0 auto 1vh auto;
    }
    .ia-panel-content {
      min-width: 0;
      max-width: 100vw;
    }
  }
